
// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
@import url(https://fonts.googleapis.com/css?family=Archivo+Black);
@import url(https://unpkg.com/font-awesome@4.7.0/css/font-awesome.min.css);


// Variables
@import "variables";

$primary: #21a8bd;

// Bulma
@import "node_modules/bulma/bulma.sass";

*, body {
    box-sizing: border-box;
}

[v-cloak] {
    display:none;
}

h1 {
    font-size: $size-1;
    margin-bottom: .7em;
}

h2 {
    font-size: $size-2;
    margin-bottom: .5em;
}

h3 {
    font-size: $size-3;
    margin-bottom: .3em;
}

.button.is-wide {
    height: auto;
    padding: .75em 0;
    width: 100%
}

body {
    background-color: $body-bg;
    background-image: url('../img/body-background.jpg');
    background-size: cover;
    color: $text-color;
    font-family: $font-family-sans-serif;
    font-size: $font-size-base;
    min-height: 100vh;

    &.has-top-navbar {
        padding-top: 4.05em;
    }
}

.nav {
    background-color: $navbar-default-bg;
    left: 50%;
    margin: 0 auto;
    min-height: 58px;
    padding: .25em 1em;
    position: fixed;
    top: 0;
    transform: translateX(-50%);
    width: 100%;

    &, a {
        color: $white;
    }

    a:hover {
        color: $primary;
    }

    li + li {
        margin-top: 0 !important;
    }

    .dropdown-menu {
        margin: 5px 0;

        .button {
            background-color: #242424;
            border: 0;
            color: #fff;
        }

        .menu {
            border: 0;
            margin-top: 5px;
            transform: translateX(-63%);
        }

        a {
            padding: 1em 3em;
        }

        a:hover {
            background-color: #242424;
        }
    }
}

.nav-brand {
    font-family: 'Archivo Black', sans-serif;
    text-transform: uppercase;
}

h2 {
    font-size: 2em;
    // margin-bottom: .7em;

    .fa {
        font-size: 2.5em;
        margin-right: .1em;
    }
}

p {
    margin-bottom: 1em;
}

.panel h2,
.panel h3,
.panel table {
    margin-bottom: 0;
}

.main-container {
    background-color: $panel-default-main-content;
    padding: 2em 4em;
}

.small-container {
    background-color: rgba(255, 255, 255, .95);
    // color: #fff;
    max-width: 400px;
    // overflow: hidden;
    padding: 2em;
    position: relative;

    h2 {
        text-align: center;

        .fa {
            color: #21a8bd;
        }
    }

    .button {
        background-color: $primary;
        border-color: lighten($primary, 15%);
        color: $white;
    }
}

.ticket-listing {
    cursor: pointer;

    .controls {
        visibility: hidden;
    }

    &:hover {
        background-color: lighten($primary, 50%);

        .controls {
            visibility: visible;
        }
    }
}

.fa.is-big {
    font-size: 2.5em;
    height: 40px;
    width: 40px;
}

.icon-box {
    background-color: $primary;
    cursor: pointer;
    height: 235px;
    padding: 3.2em 1em;

    &,
    a {
        color: $white;
    }

    &:hover {
        background-color: darken($primary, 10%);
    }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}

.tab-panes {
    border-bottom: 1px solid #dbdbdb;
    border-left: 1px solid #dbdbdb;
    border-right: 1px solid #dbdbdb;
    margin-bottom: 1em;
    margin-top: -1.5em;
    padding: 1em;
}

.pagination {
    margin-bottom: 1em;

    li {
        display: inline-block;
        list-style: none;
    }
}


